.menu {
	position   : absolute;
	right      : -20px;
	top        : 40px;
	min-width  : max-content;
	padding-top: 10px;
}


.menuContainer {
	display       : flex;
	flex-direction: column;
	padding       : 0;
	margin        : 0;
}

.menuContainer :first-child.menuName {
	border-top: none;
}

.menuList {
	padding: 0;
	margin : 0;
}

.menuName {
	font-size    : 10px;
	font-weight  : 700;
	padding      : 10px 20px;
	border-top   : 1px solid #edf2f6;
	border-bottom: 1px solid #edf2f6;
}


.menuLink {
	padding     : 5px 20px;
	margin      : 5px 10px;
	font-weight : 700;
	font-size   : 14px;
	/* min-width: max-content; */
}

.menuList :first-child {
	padding-top: 20px;
}

.signOut {
	cursor        : pointer;
	padding-bottom: 20px;
}

.signOut:hover {
	color: #FF6347;
}

.statusChange {
	display  : flex;
	min-width: max-content;
	padding  : 25px 20px 25px;
}

.statusChange input {
	height                   : 30px;
	border                   : 1px solid #e6ecf5;
	border-top-left-radius   : 3px;
	border-bottom-left-radius: 3px;
	border-right             : none;
	color                    : #515365;
	padding                  : 0 10px;
	outline                  : none;
	transition               : all .3s ease-in-out;
}

.statusChange input:focus {
	border-color: tomato;
	padding     : 0 10px;
}

.statusChange button {
	border                    : none;
	border-radius             : 0;
	border-top-right-radius   : 3px;
	border-bottom-right-radius: 3px;
	height                    : 32px;
	padding                   : 0 10px;
	background-color          : #7C5AC2;
	color                     : white;
	cursor                    : pointer;
}